@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,400;0,600;1,300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Teko&display=swap');


* {
    margin: 0%;
    padding: 0%;
    box-sizing: border-box;
}

.JabersonWebPage {
    background-color: #000000;
}

.MainBody {
    width: 100%;
    padding: 50px 20px 0 20px;


    .socialMediaLogos {
        color: rgba(192, 192, 194, 1);
        align-items: start;

        i,
        img {
            padding-left: 30px;
            padding-right: 25px;
            font-size: 20px;
            color: rgba(192, 192, 194, 1);
            border-left: 1px solid #BD0F71;

            &:first-child {
                border-left: none;
                padding-left: 0;
            }

            &:last-child {
                border-right: none;
            }
        }

    }

    .myName {
        color: rgba(192, 192, 194, 1);
        font-size: 80px;
        font-weight: 700;
        letter-spacing: 5px;
        font-family: 'Josefin Sans', sans-serif;
    }

    .designations {
        padding: 20px;
        width: 60%;
        font-size: 36px;
        display: flex;
        font-weight: 400;
        font-family: 'Josefin Sans', sans-serif;
        justify-content: center;
        align-items: center;
        color: rgba(192, 192, 194, 1);
        border: 5px solid rgba(192, 192, 194, 1);
    }
}

.AboutMe {
    width: 100%;
    height: 100vh;

    // position:static;
    .AboutMeImage {
        img {
            width: 80%;

        }
    }

    .title {
        font-size: 48px;
        color: rgba(192, 192, 194, 1);
        font-family: 'Josefin Sans', sans-serif;
        text-align: start;

    }

    .HiThere {
        span {
            text-align: center;
            align-items: center;
            display: flex;
            width: 20%;
            justify-content: center;
            padding: 20px;
            font-size: 20px;
            font-weight: 400;
            font-family: 'Josefin Sans', sans-serif;
            color: rgba(192, 192, 194, 1);
        }
    }

    .content {
        font-size: 23px;
        font-family: 'Josefin Sans', sans-serif;
        font-weight: 400;
        color: rgba(192, 192, 194, 1);
        text-align: justify;
    }

    .buttonsHere {
        transform: skewX(-15deg);

        .hereMe1 {
            display: flex;
        }
    }

    #hereMe {
        background: linear-gradient(89.71deg, #BD0F71 0.22%, #7F028A 99.72%);
        width: 200px;
        height: 100%;
        font-size: 16px;
        border-radius: 0;
        color: rgba(192, 192, 194, 1);

        span {
            transform: skewX(15deg);
        }
    }

    .fa-arrow-right {
        background: linear-gradient(89.71deg, #BD0F71 0.22%, #7F028A 99.72%);
        width: 50px;
        height: 100%;
        font-size: 30px;
        color: rgba(192, 192, 194, 1);
        display: flex;
        text-align: center;
        justify-content: center;
        align-items: center;
    }

    #CVBtns {

        border: 1px solid white;
        width: 100%;
        height: 100%;
        font-size: 20px;
        color: rgba(192, 192, 194, 1);
        display: flex;
        text-align: center;
        justify-content: center;
        align-items: center;

        span {
            transform: skewX(15deg);
        }
    }
}

.byJaberson {
    font-family: 'Josefin Sans', sans-serif;
    padding: 50px;
    background-color: rgba(31, 25, 31, 1);
    font-size: 20px;
    color: rgba(192, 192, 194, 1);
    font-weight: 400;
    line-height: 26px;
    letter-spacing: 0em;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.introductions {
    width: 100%;
    height: 80vh;
    margin-top: 150px;
}

.IntoContant {
    margin: 100px 0 0 0;
}

.ApplicationsUIUX {
    margin-top: 100px;
    background: rgba(31, 25, 31, 1);
    display: flex;
    justify-content: space-around;
    // margin: 50px 10px 0 10px;
    div {
        span {
            color: rgba(160, 160, 160, 1);
            font-family: 'Josefin Sans', sans-serif;
            font-size: 28px;
            font-weight: 600;
            line-height: 100px;
            padding: 10px;
            letter-spacing: 0.1em;
            text-align: left;


        }
    }

    img {
        width: 60px;
        height: auto;
        padding: 10px;
    }
}

@media only screen and (max-width:992px) {
    .introductions {
        margin-top: 100px;

        .socialMediaLogos {
            i {
                font-size: 18px;
            }

            padding: 5px 10px 0 0px;
        }

        .myName {
            padding-top: 40px;
            font-size: 50px;
        }

        .designations {
            margin-top: 40px;
            width: 90%;
        }
    }

    .myImage {
        img {
            width: 100%;
            margin: 0 0 0 0;
        }

    }

    .AboutMe {
        img {
            margin: 250px 0 0 0;
        }

        .HiThere {
            // width: 50%;
            text-align: center;

            span {
                width: 50%;
                // text-align: center;
                // align-items: center;
                // // display: flex;
                // justify-content: center;
            }
        }

        .content {
            text-align: justify;
        }

        .buttonsHere {

            .hereMe1 {
                text-align: center;
                align-items: center;
                display: block;
                padding: 0 20px 0px 50px;
            }
        }

        .CVBtnHere {
            padding-top: 30px;
            text-align: start;
            align-items: start;
            // display: flex;
            justify-content: start;
            width: 100%;
        }
    }

    .myImage {
        img {
            margin: 150px 0 100px 0;
        }
    }

}